@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.headerLogo{
    background-color: goldenrod;
    color:white;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    height: 70px;
}
.headerLogo img {
    width: 45px;
    height: 45px;
    padding-left: 50px;
}
.headerLogo span{
    font-size: 25px;
}
.buttons{
    display: flex;
    align-content: center;
}
.buttons input{
    font-weight: bold;
    color: white;
    height: 20px;
    margin-left: 20px;
    background-color: goldenrod;
    align-self: center;
}

button{
    margin-left: 15px;
    background-color: goldenrod;
    width: 100px;
    height: 40px;
    color: white;
    border: 1px solid goldenrod;
    border-radius: 7px;
    margin-top: 15px;
    margin-bottom: 10px;
    transition-duration: 0.4s;
}
#getRandomRecipts{
    background-color: white;
    color:goldenrod;
    width: 120px;
    height: 60px;
    font-weight: bold;
    font-size: 23px;
    margin-bottom: 30px;
}

.IngridOption{
    display: flex;
    align-content: center;
}
.IngridOption textarea{
    margin-left: 50px;
    width: 300px;
    height: 200px;
    margin-bottom: 30px;
}
.IngridOption button{
    align-self: center;
}

.ReceiptItem{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 70%;
    margin: 0 auto;
    border: 2px solid goldenrod;
    padding: 0 15px;
    margin-bottom: 30px;
}
.ReceiptItem img{
    width: 556px;
    align-self: center;
    margin-top: 15px;;
}
.ReceiptItem h2{
    align-self: center;
}
